/*--------------------------------------------------------------
# Menu Section
.--------------------------------------------------------------*/
.menu-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, 250px);
    gap: 2rem;
    padding: 0px 20px;
    justify-content: space-evenly;
}
.menu .item {
    background-color: #494438;
    border-radius: 15px 15px 15px 30px;
    overflow: hidden;
    min-height: 1px;
    display: inline-grid;
    position: relative;
}

.menu .menu-img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    object-position: center;
}

.menu .menu-content {
    padding: 20px;
}

.menu .menu-content p {
    padding-right: 10px;
    position: relative;
    z-index: 3;
    font-weight: 700;
    color: white;
    transition: 0.3s;
    font-size: 26px;
    margin-bottom: 5px;
}

.menu .item span {
    position: relative;
    color: #cda45e;
    font-size: 28px;
    font-weight: bold;
}

.menu .menu-ingredients {
    font-style: italic;
    font-size: 12px;
    font-family: "Poppins", sans-serif;
    color: rgba(255, 255, 255, 0.6);
}

