/* Additional Styling By Crimson */
.validate {
    opacity: 0;
    transition: all 0.5s ease;
    margin-top: 5px;
    color: var(--red);
}
.validate.show {
    opacity: 1;
}

.nav-menu > ul > li.cart {
    padding: 8px;
    width: 2.5em;
    height: 2.5em;
    background: #cda45e;
    margin-left: 20px;
    display: grid;
    place-items: center;
    border-radius: 50%;
    cursor: pointer;
}

.item .cta {
    display: grid;
    grid-template-columns: 5em auto;
    grid-template-rows: 5em;
    place-items: center;
    align-self: flex-end;
}

.item .cta button {
    border: none;
    outline: none;
    width: 100%;
    height: 100%;
    background: #cda45e;
    border-radius: 0px 30px 0px 30px;
    box-shadow: 5px -5px 10px rgba(0, 0, 0, 0.3) !important;
    transition: filter 0.3s linear;
}

.item .cta button:active {
    filter: brightness(70%);
}

.fav {
    position: absolute;
    top: 0;
    right: 0;
    padding: 15px;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 0px 15px 0px 30px;
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
}

.fav svg {
    color: #cda45e;
}

.item:hover .fav {
    opacity: 1;
    pointer-events: all;
}

.shopList {
    display: flex;
    justify-content: space-between;
}

.rest {
    border: none;
    outline: none;
    background: none;
    color: #cda45e;
    display: flex;
    align-items: center;
    transition: filter 0.5s ease-out;
}

.rest:hover {
    filter: brightness(150%);
}

.rest p {
    font-size: 16px;
    font-weight: 200;
    font-family: "Open Sans", "San-serif";
    margin-right: 10px;
}

.rest span {
    display: inline-grid;
    place-items: center;
    border: 2px solid #cda45e;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
}
